.Joke {
    display: flex;
    border-bottom: 2px solid #eee;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    padding: 1rem;
    
}
.Joke-buttons {
    display: flex;
    margin-right: 1rem;
    align-items: center;
    justify-content: center;
    width: 15%;
}
.Joke-text {
    width: 75%;
    font-size: 1.2rem;
}
.Joke-votes {
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    text-align: center;
    font-size: 20px;
    font-weight: 300;
    border: 3px solid red;
    box-shadow: 0 19px 38px rgba(0,0,0,0.3), 0 15px 12px rgba(0,0,0, 0.1);
}

.fa-arrow-up, .fa-arrow-down {
    font-size: 1.5em;
    margin: 10px;
    cursor: pointer;
}

.Joke-emoji {
    font-size: 2rem;
    margin-left: auto;
    border-radius: 50%;
    box-shadow: 0 19px 38px rgba(0,0,0,0.3), 0 15px 12px rgba(0,0,0, 0.1);
}